// Changing the body background and text color
$body-bg: #ffffff;
$body-color: #212529;

// Changing the theme colors (ex. classes btn-primary, bg-secondary)
$primary: #327ae7;
$secondary: #6c757d;
$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;
$light: #f8f9fa;
$dark: #343a40;

//Adding an additional theme color (ex. classes btn-cool, bg-cool)
// $theme-colors: (
//  "cool": #4d3fa3,
// );

// // Spacing
$spacer: 1rem;
$spacers: (0: 0, 1: $spacer, 15: $spacer * 1.5, 2: $spacer * 2, 25: $spacer * 2.5, 3: $spacer * 3, 35: $spacer * 3.5, 4: $spacer * 4, 45: $spacer * 4.5, 5: $spacer * 5, 55: $spacer * 5.5, 6: $spacer * 6, 65: $spacer * 6.5, 7: $spacer * 7, 75: $spacer * 7.5,8: $spacer * 8, 85: $spacer * 8.5, 9: $spacer * 9, 95: $spacer * 9.5, 100: $spacer * 10);

$link-color: $dark;
$link-decoration: none;
$link-hover-color: $secondary;
$link-hover-decoration: none;
$line-height-lg: 1.5;
$line-height-sm: 1.5;
$border-width: 1px;
$border-radius: 1rem;
$border-radius-lg: 3rem;
$border-radius-sm: 0.5rem;

// // Fonts
$font-family-sans-serif: sans-serif;
// $font-family-base: 'Montserrat', $font-family-sans-serif;
$font-size-base: 1.6rem;
$rfs-base-font-size: 1.4rem;
$font-size-lg: $font-size-base;
$font-size-sm: $font-size-base;
$line-height-base: 1.5;
$h1-font-size: 4rem;
$h2-font-size: 3.5rem;
$h3-font-size: 3rem;
$h4-font-size: 2.5rem;
$h5-font-size: 2rem;
$h6-font-size: 1.8rem;
$headings-margin-bottom: 2rem;
// $headings-font-family: 'Monstserrat', $font-family-sans-serif;
$headings-font-weight: 700;
$headings-line-height: 1;
$headings-color: $primary;
$lead-font-size: $font-size-base;
$lead-font-weight: 400;
$small-font-size: $font-size-base * 0.6666666667;
$blockquote-font-size: $font-size-base * 1.3333333333;
$blockquote-line-height: 1;
$hr-border-color: rgba($dark, 0.1);
$mark-padding: 1rem;
$list-inline-padding: 1rem;

// // Buttons
$input-btn-padding-y: 0.5rem;
$input-btn-padding-x: 1.5rem;
$input-btn-focus-color: rgba($secondary, 0.25);
$input-btn-padding-y-sm: 0rem;
$input-btn-padding-x-sm: 0.5rem;
$input-btn-line-height-sm: $line-height-sm;
$input-btn-padding-y-lg: 1rem;
$input-btn-padding-x-lg: 1rem;
$input-btn-line-height-lg: $line-height-lg;
$btn-link-disabled-color: $dark;
$btn-block-spacing-y: 1rem;
$btn-border-radius: 0.5rem;
$btn-border-radius-lg: 0.5rem;
$btn-border-radius-sm: 0.5rem;

// // Forms
$input-color: $dark;

// For form controls and buttons
$input-box-shadow: inset 0 1px 1px rgba($dark, 0.075);
$input-border-radius: $border-radius-sm;
$input-border-radius-lg: $border-radius-sm;
$input-border-radius-sm: $border-radius-sm;
$input-focus-border-color: $secondary;

// // Navs
$nav-link-padding-y: 1.5rem;
// // Navbar
$navbar-padding-y: 0;
$navbar-padding-x: 0;
$navbar-brand-padding-y: 0rem;
$navbar-toggler-padding-y: 1rem;
$navbar-toggler-padding-x: 0rem;
$navbar-dark-toggler-border-color: rgba($light, 0.1) !default;
$navbar-light-toggler-border-color: rgba($dark, 0.1) !default;
